	module.exports = {"data":{"post":{"html":"<p>It's a men's weekend at home. Mama is off to the Alps until Monday for a 'team building' ski holiday organized by her boss, although given her injured ankle and weak knee, the only winter sports she'll be participating in willbe those that require the butt to be firmly planted on the ground. Can one start aprés ski at noon?</p>\n<p>Meanwhile, Christopher and I are home alone. Maybe I can slip him off on Oma for a couple of hours on Sunday. </p>\n<p><b>Update</b> Mama sends this photo greeting from their <em>Hütte</em> on the slopes. Looks a lot nicer than the grey skies here up north! </p>\n<p><img alt=\"Blue skies in Austria\" src=\"http://www.papascott.de/wordpress/wp-content/uploads/2003/02/berge2.jpg\" width=\"325\" height=\"244\" border=\"0\" /></p>\n<p><img alt=\"Ski heil!\" src=\"http://www.papascott.de/wordpress/wp-content/uploads/2003/02/skiheil.jpg\" width=\"325\" height=\"244\" border=\"0\" /></p>","fields":{"title":"Long Weekend","date":"21 Feb 2003","url":"https://papascott.de/2003-02-21-long-weekend/"}}},"pathContext":{"slug":"/2003-02-21-long-weekend/","prev":{"fields":{"title":"German Practice","date":"21 Feb 2003","slug":"/2003-02-21-german-practice/"}},"next":{"fields":{"title":"Mr. Christopher Goes To Town","date":"22 Feb 2003","slug":"/2003-02-22-mr-christopher-goes-to-town/"}}}}

